(function() {
  'use strict';

  angular
    .module('foodieApp')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, webDevTec, toastr) {
    var vm = this;

    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.creationDate = 1462632090525;
    vm.showToastr = showToastr;

    activate();

    function activate() {
      getWebDevTec();
      $timeout(function() {
        vm.classAnimation = 'rubberBand';
      }, 4000);
    }

    function showToastr() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = '';
    }

    function getWebDevTec() {
      vm.awesomeThings = webDevTec.getTec();

      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    }
  }
})();


angular
    .module('foodieApp')
    .controller('HomeController', HomeController);

HomeController.$inject = ['vendorService', '$log', '$location'];

function HomeController(vendorService, $log, $location) {
    var vm = this;
    vm.vendors = [];
    vm.setVendor = function(vendor) {
        vendorService.setClickedVendor(vendor);
        $log.info(vendor);
        $location.path('/menuItem');
    }

    activate();

    function activate() {
        return getVendors().then(function() {
            $log.info('Activated Vendors View');
        });
    }

    function getVendors() {
        return vendorService.getVendors()
            .then(function(data) {
                vm.vendors = data;
                return vm.vendors;
            });
    }
}

